            cancel-text="Отмена"
            @cancel="cancelExportSettings"
            @accept="acceptExportSettings"
            @close="cancelExportSettings"
            :active.sync="activeExportSettings">
            <div class="con-exemple-prompt">
                <div class="mb-1">Выберите накопитель</div>
                <h5 v-if="!$store.getters['settings/drive'].length" class="mt-2">Вставьте USB FLASH накопитель!</h5>
                <v-select :clearable="false" v-if="$store.getters['settings/drive'].length" v-model="valExportSettings" :options="$store.getters['settings/drive']"></v-select><br>
