</template>

<script>
    import vSelect from 'vue-select'

    export default {
        components: {
            'v-select': vSelect,
        },
        computed: {
            valExportSettings: {
                get(){
                    this.pathUsb = this.$store.getters['settings/drive'][0];
                    return this.$store.getters['settings/drive'][0]
                },
                set(val){
                    this.pathUsb = val
                }
            }
        },
        data(){
            return {
                activeExportSettings: false,
                activeExportMac: false,
                pathUsb: null
            }
        },
        methods:{
            acceptExportSettings(){
                if(this.pathUsb.id){
                    this.$io.emit('export:settings', this.pathUsb.id)
                }
            },
            cancelExportSettings(){
                this.pathUsb = null;
                this.activeExportSettings = false;
            },
            acceptExportMac(){
                if(this.pathUsb.id){
                    this.$io.emit('export:mac', this.pathUsb.id)
                }
            },
            cancelExportMac(){
                this.pathUsb = null;
                this.activeExportMac = false;
            }
        }
    }
</script>

